.product {
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  overflow: hidden;
  transition: box-shadow 0.3s ease;

  &:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }

  .card-img-top {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }

  .card-body {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 1rem;
  }

  .card-title {
    font-size: 1.1rem;
    margin-bottom: 0.5rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .card-text {
    flex-grow: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    font-size: 1.4rem;
    margin-bottom: 0.5rem;
  }

  .ratings {
    font-size: 1.2rem;
    color: #fdcc0d;
  }

  #no_of_reviews {
    font-size: 0.85rem;
    color: grey;
    margin-left: 0.5rem;
  }

  .btn {
    margin-top: auto;
  }
}
.card {
  height: 100%;

  &-title {
    a {
      color: #2e2e2e;
      text-decoration: none;

      &:hover {
        color: #fa9c23;
        text-decoration: none;
      }
    }
  }

  &-body {
    padding-left: 0;
  }

  &-text {
    font-size: 1.4rem;
  }

  &-img-top {
    width: 200px;
    height: 150px;
  }
}

.ratings {
  font-size: 1.2rem;
  color: #fdcc0d;
}

#no_of_reviews {
  font-size: 0.85rem;
  color: grey;
  margin-left: 0.5rem;
}