.chat {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 80px); // Header yüksekliğini çıkarın
  width: 100%;
  
  &-container {
    flex: 1;
    display: flex;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    border: 1px solid $grey-color;
    overflow: hidden;
  }

  &-sidebar {
    width: 250px;
    border-right: 1px solid $grey-color;
    overflow-y: auto;
  }

  &-users {
    .chat-user {
      padding: 10px;
      cursor: pointer;
      &:hover, &.selected {
        background-color: $light-grey-color;
      }
    }
  }

  &-main {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }

  &-messages {
    flex: 1;
    overflow-y: auto;
    padding: 20px;
    display: flex;
    flex-direction: column;
  }

  &-message {
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 10px;
    max-width: 70%;
    word-wrap: break-word;

    &.sent {
      align-self: flex-end;
      background-color: $primary-color;
      color: white;
    }

    &.received {
      align-self: flex-start;
      background-color: $light-grey-color;
    }

    &-content {
      display: block;
      margin-bottom: 5px;
    }

    &-time {
      display: block;
      font-size: 0.8em;
      text-align: right;
    }
  }

  &-form {
    display: flex;
    padding: 20px;
    border-top: 1px solid $grey-color;
    background-color: $white-color;
  }

  &-input {
    flex: 1;
    padding: 10px;
    border: 1px solid $grey-color;
    border-radius: 5px;
  }

  &-button {
    margin-left: 10px;
    padding: 10px 20px;
    background-color: $primary-color;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;

    &:disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }
  }
}

@media (max-width: 1200px) {
  .chat-container {
    max-width: 95%;
  }
}

@media (max-width: 768px) {
  .chat {
    height: calc(100vh - 60px); // Mobil için header yüksekliğini ayarlayın

    &-container {
      flex-direction: column;
    }

    &-sidebar {
      width: 100%;
      height: 100px;
      border-right: none;
      border-bottom: 1px solid $grey-color;
    }

    &-users {
      display: flex;
      overflow-x: auto;
    }

    &-main {
      flex: 1;
    }

    &-message {
      max-width: 85%;
    }

    &-form {
      flex-direction: column;
    }

    &-input {
      margin-bottom: 10px;
    }

    &-button {
      margin-left: 0;
      width: 100%;
    }
  }
}

@media (max-width: 480px) {
  .chat {
    &-message {
      max-width: 95%;
    }
  }
}