@import '../base/custom-variables';

/* Home page */
#products_heading {
  margin-top: 1.8rem;
}

/* Product Details Page */
#product_id {
  color: grey;
  font-size: 0.8rem;
}

#product_price {
  font-size: 2rem;
  font-weight: bold;
}

#product_seller {
  color: grey;
  font-size: 0.9rem;
}

#stock_status {
  font-size: 1.1rem;
  font-weight: bold;
  margin-left: 0.3rem;
}

#product_image {
  margin-top: 9rem;
}

/* Cart Page */
.cart-item {
  margin: 1.8rem 0;
  padding: 0 0.7rem;

  a {
    color: grey;
  }
}

#delete_cart_item {
  color: $red-color;
  background: $white-color;
  padding: 0.3rem 0.5rem;
  font-size: 1.1rem;
  border: none;
}

#card_item_price {
  color: #febd69;
  font-weight: bolder;
  font-size: 1rem;
}

#order_summary {
  border: 1px solid $grey-color;
  border-radius: 1rem;
  padding: 2rem 1.5rem;
}

.order-summary-values {
  float: right;
  font-weight: bold;
}

/* Login & Register */
.wrapper {
  form {
    .btn,
    .fetch-btn {
      background-color: $primary-color;
      border-color: $primary-color;
      color: $white-color;
      margin-top: 1rem;
    }

    a {
      font-size: 0.9rem;
      color: grey;
    }
  }
}

/* User Profile */
.avatar-profile {
  height: 16rem;
  width: 16rem;
}

.user-info {
  h4 {
    font-weight: 800;
    color: grey;
    margin-top: 2rem;
  }

  p {
    font-size: 1.1rem;
  }
}

#edit_profile {
  background-color: $primary-color;
  border-color: $primary-color;
}

/* Admin Dashboard */
.card-font-size {
  font-size: 1.5rem;
}
