// _hero.scss

@import '../base/custom-variables';
@import '../base/mixins';

.hero {
  &_section {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 550px;
    padding: 0 60px;
    background-color: $black-color;
    color: $white-color;

    > div {
      text-align: center;
      flex-direction: column;
      justify-content: center;
    }
  }

  &_title {
    padding-top: 100px;
    font-size: 45px;
    font-weight: 700;
    margin: 30px auto 0;
  }

  &_subtitle {
    font-size: 24px;
    margin: 0 auto 30px;
    width: 70%;
  }

  &_link {
    padding: 16px 32px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 700;
    border: 2px solid $white-color;
    border-radius: 32px;
    background-color: $white-color;
    color: $black-color;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
    text-decoration: none;
    margin-top: 30px;
    display: inline-block;

    &:hover {
      background-color: transparent;
      color: $white-color;
    }
  }

  &_image {
    margin-top: 30px;
    padding: 30px;
    margin-left: 30px;
    transition: all 0.3s ease-in-out;
    height: 500px;

    &:hover {
      transform: scale(1.05);
    }
  }

  &-container {
    transition: all 1s ease-in-out;

    &.animating {
      animation: blackhole 1s forwards;
    }
  }
}

@keyframes blackhole {
  0% {
    transform: scale(1) rotate(0deg);
    opacity: 1;
  }
  100% {
    transform: scale(0) rotate(720deg);
    opacity: 0;
  }
}

// Responsive tasarım için
@include respond-to('large') {
  .hero {
    &_section {
      grid-template-columns: 1fr;
      height: auto;
      padding: 30px;
    }

    &_image {
      margin-left: 0;
      height: auto;
      max-width: 100%;
    }
  }
}

@include respond-to('medium') {
  .hero {
    &_title {
      font-size: 36px;
      padding-top: 50px;
    }

    &_subtitle {
      font-size: 20px;
      width: 90%;
    }

    &_link {
      padding: 12px 24px;
    }
  }
}

@include respond-to('small') {
  .hero {
    &_title {
      font-size: 28px;
    }

    &_subtitle {
      font-size: 18px;
    }

    &_link {
      padding: 10px 20px;
      font-size: 14px;
    }
  }
}
