// _featured-products.scss

@import '../base/custom-variables';
@import '../base/mixins';

.featured_products {
  margin: 65px;

  h2 {
    font-size: 48px;
    text-align: center;
    margin-bottom: 65px;
  }

  &_list {
    margin-bottom: 65px;
    justify-content: space-evenly;
    display: flex;
    flex-wrap: wrap;
  }
}

// Responsive tasarım için
@include respond-to('large') {
  .featured_products {
    margin: 40px;

    h2 {
      font-size: 40px;
      margin-bottom: 40px;
    }
  }
}

@include respond-to('medium') {
  .featured_products {
    margin: 30px;

    h2 {
      font-size: 36px;
      margin-bottom: 30px;
    }
  }
}

@include respond-to('small') {
  .featured_products {
    margin: 20px;

    h2 {
      font-size: 28px;
      margin-bottom: 20px;
    }

    &_list {
      justify-content: center;
    }
  }
}
