@import '../base/custom-variables';
@import '../base/mixins';

.new-product-form {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;

  h2 {
    font-size: 1.8rem;
    margin-bottom: 1.5rem;
  }

  .form-label {
    font-weight: 600;
  }

  .form-control,
  .form-select {
    margin-bottom: 1rem;
  }

  .btn {
    background-color: $primary-color;
    color: white;
    &:hover {
      background-color: darken($primary-color, 10%);
    }
  }

  @include respond-to('medium') {
    padding: 15px;

    h2 {
      font-size: 1.5rem;
    }
  }

  @include respond-to('small') {
    padding: 10px;

    h2 {
      font-size: 1.3rem;
    }

    .row {
      flex-direction: column;
    }

    .col {
      width: 100%;
    }
  }

  @media (max-width: 370px) {
    h2 {
      font-size: 1.1rem;
    }

    .form-label {
      font-size: 0.9rem;
    }

    .form-control,
    .form-select {
      font-size: 0.9rem;
    }

    .btn {
      font-size: 0.9rem;
    }
  }
}
