.checkout-progress {
  display: flex;
  justify-content: space-between;
  margin: 20px auto;
  width: auto;
  font-family: sans-serif;
  overflow: hidden; // overflow: auto yerine hidden kullanıyoruz

  div {
    position: relative;
    padding: 5px 10px 5px 15px;
    color: grey;
    font-weight: bold;
    letter-spacing: 1px;
    line-height: 30px;
    text-decoration: none;
    cursor: default;
    flex: 1;
    text-align: center;

    &::before,
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 50%;
      z-index: -1;
    }

    &::after {
      left: 50%;
      border-left: 1px solid #fff;
    }
  }

  .active-step {
    background: #fa9c23;
    color: #fff;

    &::before,
    &::after {
      background: #fa9c23;
    }
  }

  .incomplete {
    background: #eeeeee;

    &::before,
    &::after {
      background: #eeeeee;
    }
  }

  div:first-child::before {
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
  }

  div:last-child::after {
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
  }
}