/* Cart */
.cart-item {
  margin: 1.8rem 0rem;
  padding: 0rem 0.7rem;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #eee;

  a {
    color: grey;
  }

  img {
    max-width: 5rem;
    height: auto;
  }

  > div {
    margin-right: 1rem;
  }
}

#delete_cart_item {
  color: red;
  background: white;
  padding: 0.3rem 0.5rem;
  font-size: 1.1rem;
  border: none;
  cursor: pointer;
}

#card_item_price {
  color: #febd69;
  font-weight: bolder;
  font-size: 1rem;
}

#checkout_btn,
.review-btn {
  background-color: #fa9c23;
  border-color: #fa9c23;
  margin-top: 2rem;
  border-radius: 5rem;
}

#view_order_details {
  background-color: #fa9c23;
  border-color: #fa9c23;
}

#order_summary {
  border: 1px solid #e3e3e3;
  border-radius: 1rem;
  padding: 2rem 1.5rem;
}

.order-summary-values {
  float: right;
  font-weight: bold;
}

.stockCounter {
  display: inline-flex;
  align-items: center;

  input {
    border: none;
    width: 3rem;
    text-align: center;
    padding: 0.25rem 0.5rem;
    font-size: 0.875rem;
    line-height: 1.5;
  }

  .btn {
    padding: 0.25rem 0.5rem;
    font-size: 0.875rem;
    line-height: 1.5;
  }

  .plus,
  .minus {
    padding: 0.1rem 0.5rem;
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}

@media (max-width: 768px) {
  .cart-item {
    flex-direction: column;
    align-items: flex-start;

    > div {
      margin-right: 0;
      margin-bottom: 1rem;
    }
  }

  #delete_cart_item {
    margin-top: 1rem;
  }

  #order_summary {
    margin-top: 2rem;
  }
}