.chat-notifications {
  max-height: 300px;
  overflow-y: auto;
}

.chat-notification-item {
  padding: 10px;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &:last-child {
    border-bottom: none;
  }

  span {
    cursor: pointer;
  }

  button {
    background-color: #f44336;
    color: white;
    border: none;
    padding: 5px 10px;
    cursor: pointer;

    &:hover {
      background-color: darken(#f44336, 10%);
    }
  }
}

.no-notifications {
  padding: 10px;
  text-align: center;
  color: #888;
}