@import '../base/custom-variables';
@import '../base/mixins';

.stockCounter {
  display: inline-flex;
  align-items: center;

  input {
    border: none;
    width: 3rem;
    text-align: center;
  }

  .plus,
  .minus {
    padding: 0.1rem 0.5rem;
    cursor: pointer;
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}

.cart-item {
  .stockCounter {
    margin-top: 1rem;
  }
}

// Yeni eklenen stiller
#product_image {
  .main-image-wrapper {
    width: 100%;
    padding-top: 100%; // 1:1 aspect ratio
    position: relative;
    overflow: hidden;
    margin-bottom: 1rem;
  }

  .main-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  .thumbnails {
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
    margin-top: 1rem;
  }

  .thumbnail-wrapper {
    width: calc(25% - 10px);
    margin: 5px;
    aspect-ratio: 1 / 1;
    @include respond-to('medium') {
      width: calc(20% - 10px);
    }
    @include respond-to('large') {
      width: calc(16.666% - 10px);
    }
  }

  .thumbnail {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: 2px solid $light-grey-color;
    transition: border-color 0.3s ease;
    cursor: pointer;

    &:hover, &.active {
      border-color: $primary-color;
    }
  }
}
// Responsive tasarım için
@include respond-to('medium') {
  #product_image {
    .thumbnail-container {
      padding: 0.3rem;
      max-width: 160px; // Orta boyutlu ekranlar için daha büyük
    }
  }
}

@include respond-to('small') {
  #product_image {
    .row {
      justify-content: center;
    }

    .thumbnail-container {
      max-width: 120px; // Küçük ekranlar için daha büyük
    }
  }
}
