// _base.scss

@import 'custom-variables';
@import 'mixins';

// Reset
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

// Base styles
html,
body {
  font-family: $main-font;
  font-size: 16px;
  line-height: 1.5;
  color: $text-color;
  background-color: $background-color;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

// App structure
.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

// Typography
h1, h2, h3, h4, h5, h6 {
  margin-bottom: 0.5em;
  font-weight: 700;
  line-height: 1.2;
}

p {
  margin-bottom: 1em;
}

a {
  color: $primary-color;
  text-decoration: none;
  transition: color 0.3s ease;

  &:hover {
    color: darken($primary-color, 10%);
  }
}

// Lists
ul, ol {
  margin-bottom: 1em;
  padding-left: 1.5em;
}

// Images
img {
  max-width: 100%;
  height: auto;
}

// Form elements
input, textarea {
  -webkit-user-select: text;
  user-select: text;
}

// Utility classes
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.text-center {
  text-align: center;
}

.hidden {
  display: none !important;
}

// Scroll Bar
::-webkit-scrollbar {
  width: 8px;
  background-color: $background-color;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(66, 66, 66, 0.2);
  border: 0;
  background-clip: padding-box;
  border-radius: 5px;
}

// Footer
.footer {
  flex-shrink: 0;
  padding: 10px;
  background-color: $footer-bg-color;
  text-align: center;
}

// Responsive typography
@include respond-to('medium') {
  html {
    font-size: 14px;
  }
  
  .footer {
    font-size: 0.9em;
  }
}

@include respond-to('small') {
  html {
    font-size: 12px;
  }
  
  .footer {
    font-size: 0.8em;
    padding: 5px;
  }
}