// _list-base.scss
@import '../base/mixins';

%responsive-list {
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;

  table {
    min-width: 100%;
    border-collapse: collapse;
  }

  th,
  td {
    white-space: nowrap;
    padding: 0.5rem;
  }

  @include respond-to('medium') {
    font-size: 0.9rem;

    .btn {
      padding: 0.25rem 0.5rem;
      font-size: 0.8rem;
    }
  }

  @include respond-to('small') {
    font-size: 0.8rem;

    .btn {
      padding: 0.2rem 0.4rem;
      font-size: 0.7rem;
    }
  }
}
