// _invoice.scss
@import '../base/custom-variables';
@import '../base/mixins';

.clearfix {
  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

.order-invoice {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;

  #order_invoice {
    width: 100%;
    background-color: white;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }

  a {
    color: #5d6975;
    text-decoration: underline;
  }

  header {
    padding: 10px 0;
    margin-bottom: 30px;
  }

  h1 {
    border-top: 1px solid #5d6975;
    border-bottom: 1px solid #5d6975;
    color: #5d6975;
    font-size: 2.4em;
    line-height: 1.4em;
    font-weight: normal;
    text-align: center;
    margin: 0 0 20px 0;
    background: url('../../../public/images/dimension.png');
  }

  table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    margin-bottom: 20px;

    tr:nth-child(2n-1) td {
      background: #f5f5f5;
    }

    th,
    td {
      text-align: center;
    }

    th {
      padding: 5px 20px;
      color: #5d6975;
      border-bottom: 1px solid #c1ced9;
      white-space: nowrap;
      font-weight: normal;
    }

    .service,
    .desc {
      text-align: left;
    }

    td {
      padding: 20px;
      text-align: right;

      &.service,
      &.desc {
        vertical-align: top;
      }

      &.unit,
      &.qty,
      &.total {
        font-size: 1.2em;
      }

      &.grand {
        border-top: 1px solid #5d6975;
      }
    }
  }

  footer {
    color: #5d6975;
    width: 100%;
    height: 30px;
    border-top: 1px solid #c1ced9;
    padding: 8px 0;
    text-align: center;
  }
}

#logo {
  text-align: center;
  margin-bottom: 10px;

  img {
    width: 180px;
  }
}

#project {
  float: left;

  span {
    color: #5d6975;
    text-align: right;
    width: 52px;
    margin-right: 10px;
    display: inline-block;
    font-size: 0.8em;
  }
}

#company {
  float: right;
  text-align: right;
}

#project,
#company {
  div {
    white-space: nowrap;
  }
}

#notices {
  .notice {
    color: #5d6975;
    font-size: 1.2em;
  }
}

@include respond-to('medium') {
  .order-invoice {
    h1 {
      font-size: 2em;
    }

    table {
      font-size: 0.9em;
    }
  }
}

@include respond-to('small') {
  .order-invoice {
    h1 {
      font-size: 1.8em;
    }

    table {
      font-size: 0.8em;
    }
  }

  #project,
  #company {
    float: none;
    text-align: left;
    margin-bottom: 20px;
  }
}

@media (max-width: 1000px) {
  .order-invoice {
    padding: 10px;
  }
}

@media (max-width: 691px) {
  .order-invoice {
    padding: 10px;

    h1 {
      font-size: 1.5em;
    }

    table {
      font-size: 0.7em;

      th,
      td {
        padding: 5px;
      }

      .service,
      .desc {
        max-width: 100px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }

  #logo img {
    width: 120px;
  }

  #project,
  #company {
    float: none;
    text-align: left;
    margin-bottom: 10px;

    div {
      margin-bottom: 5px;
    }
  }

  #project span {
    width: auto;
    display: inline;
    margin-right: 5px;
  }

  #notices .notice {
    font-size: 1em;
  }
}

@media (max-width: 591px) {
  .order-invoice {
    padding: 5px;

    h1 {
      font-size: 1.2em;
    }

    table {
      font-size: 0.6em;

      th,
      td {
        padding: 3px;
      }

      .service,
      .desc {
        max-width: 80px;
      }
    }
  }

  #logo img {
    width: 100px;
  }

  #project,
  #company {
    font-size: 0.8em;
  }

  #notices .notice {
    font-size: 0.9em;
  }
}

@media (max-width: 400px) {
  .order-invoice {
    table {
      font-size: 0.6em;

      th,
      td {
        padding: 3px;
      }

      .service,
      .desc {
        max-width: 60px;
      }
    }

    #logo img {
      width: 80px;
    }

    #project,
    #company {
      font-size: 0.7em;
    }

    #notices .notice {
      font-size: 0.8em;
    }
  }
}

@media (max-width: 300px) {
  .order-invoice {
    table {
      display: block;
      overflow-x: auto;
      white-space: nowrap;
    }
  }
}

@media print {
  .order-invoice {
    width: 100%;
    max-width: none;
    margin: 0;
    padding: 0;

    table {
      page-break-inside: auto;

      tr {
        page-break-inside: avoid;
        page-break-after: auto;
      }
    }
  }
}

.print-address {
  display: flex;
  flex-direction: column;

  span {
    font-weight: bold;
    margin-bottom: 5px;
  }

  div {
    margin-bottom: 2px;
  }
}

@media print {
  .print-address {
    page-break-inside: avoid;
  }
}
