@import '../base/custom-variables';
@import '../base/mixins';

// Diğer stiller...
nav {
  background-color: $secondary-color;
  padding: 1rem;
}

.navbar {
  padding: 0.1rem 1rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.navbar-nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

@include respond-to('medium') {
  .navbar-brand img {
    max-width: 100px;
  }

  .navbar .btn,
  .navbar .nav-link {
    font-size: 0.9rem;
    padding: 0.25rem 0.5rem;
  }
}

@media (max-width: 576px) {
  .navbar {
    justify-content: center;
  }

  .navbar-brand {
    margin-bottom: 0.5rem;
  }

  .navbar-nav {
    justify-content: center;
  }

  .navbar .nav-link,
  .navbar .btn {
    margin: 0.25rem;
  }
}

footer {
  margin-top: auto;
  color: grey;
  bottom: 0;
  width: 100%;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
  font-weight: 700;

  form {
    padding: 2.5rem 3rem;
  }
}

@include respond-to('medium') {
  .navbar-brand img {
    max-width: 100px;
  }

  .navbar .btn,
  .navbar .nav-link {
    font-size: 0.9rem;
    padding: 0.25rem 0.5rem;
  }
}
