@import '../base/custom-variables';
@import '../base/mixins';

/* Buttons */
#login_btn,
#view_btn,
#new_review_btn,
#cart_btn,
#review_btn,
.update-btn,
#create_btn,
#checkout_btn,
.review-btn {
  background-color: $primary-color;
  color: $white-color;
  padding: 0.4rem 1.8rem;
  border: none;
}

/* Search */
#search_btn {
  background-color: #febd69;
  height: 100%;
  padding: 0.4rem 1rem;
  border: none;
  position: absolute;
  right: 0;
  top: 0;
  border-radius: 0 0.25rem 0.25rem 0;
}

.input-group {
  width: 90%;
  margin-right: 35%;
  position: relative;
}

#search_field {
  height: 2.4rem;
  padding: 1rem;
  padding-right: 3rem;
  border-radius: 0.25rem;
}

/* Cart */
#cart {
  font-size: 1rem;
  color: $white-color;
}

#cart_count {
  background-color: #febd69;
  padding: 0.2rem 0.5rem;
  border-radius: 0.2rem;
  color: black;
  font-weight: bold;
  font-size: 0.9rem;
}

/* Avatar */
.avatar {
  display: inline-block;
  margin-bottom: 0;
  height: 3rem;
  width: 3rem;
  border-radius: 50%;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.avatar-nav {
  margin-right: 0.6rem;
  height: 2.2rem;
  width: 2.2rem;
}

/* Loader */
.loader {
  display: block;
  margin: 20% auto 0;
  width: 80px;
  height: 80px;
  padding-left: 0;

  &:after {
    content: ' ';
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid $primary-color;
    border-color: $primary-color transparent;
    animation: lds-dual-ring 1.2s linear infinite;
  }
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Notifications */
.notification-dropdown {
  position: relative;
  display: inline-block;
}

.notification-menu {
  position: absolute;
  right: 0;
  background-color: #f9f9f9;
  min-width: 250px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  max-height: 300px;
  overflow-y: auto;
}

.notification-item {
  padding: 12px 16px;
  display: block;
  color: black;
  border-bottom: 1px solid #ddd;

  &:last-child {
    border-bottom: none;
  }

  &.unread {
    background-color: #e6f3ff;
  }
}

.notification-count {
  background-color: $red-color;
  color: $white-color;
  border-radius: 50%;
  padding: 0.25rem 0.5rem;
  font-size: 0.75rem;
  position: absolute;
  top: -8px;
  right: -8px;
  min-width: 1.5rem;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Chat Notifications */
.chat-notification-dropdown {
  @extend .notification-dropdown;
}

.chat-notification-button {
  background-color: $secondary-color;
  color: $white-color;
  border: none;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  cursor: pointer;
  position: relative;
}

.chat-notification-menu {
  @extend .notification-menu;
  background-color: $white-color;
  border-radius: 4px;
  max-height: 400px;
}

.chat-notification-item {
  @extend .notification-item;
  display: flex;
  justify-content: space-between;
  align-items: center;

  span {
    flex-grow: 1;
    margin-right: 10px;
  }

  button {
    background-color: $primary-color;
    color: $white-color;
    border: none;
    padding: 5px 10px;
    border-radius: 3px;
    cursor: pointer;
  }
}

.chat-notification-badge {
  @extend .notification-count;
  background-color: $primary-color;
}

@include respond-to('medium') {
  .chat-notification-button {
    font-size: 0.9rem;
    padding: 0.25rem 0.5rem;
  }

  .chat-notification-menu {
    min-width: 200px;
  }
}
