.home {
  .row {
    @media (max-width: 768px) {
      flex-direction: column;
    }
  }

  #products_heading {
    @media (max-width: 768px) {
      font-size: 1.5rem;
      margin-top: 1rem;
    }
  }

  .col-6.col-md-3 {
    @media (max-width: 768px) {
      width: 100%;
      margin-bottom: 1rem;
    }
  }

  .col-6.col-md-9,
  .col-6.col-md-12 {
    @media (max-width: 768px) {
      width: 100%;
    }
  }

  #products {
    .row {
      @media (max-width: 768px) {
        justify-content: center;
      }
    }
  }
}

.filters {
  @media (max-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    > div {
      width: 48%;
      margin-bottom: 1rem;
    }
  }

  @media (max-width: 480px) {
    > div {
      width: 100%;
    }
  }
}
