@import '../base/mixins';

.iyzipay-info-manager {
  max-width: 800px;
  margin: 0 auto;

  h2 {
    font-size: 1.8rem;
    
    @include respond-to('small') {
      font-size: 1.5rem;
    }
  }

  .form-control {
    font-size: 1rem;
    
    @include respond-to('small') {
      font-size: 0.9rem;
    }
  }

  .btn {
    @include respond-to('small') {
      width: 100%;
      margin-bottom: 0.5rem;
    }
  }

  @include respond-to('medium') {
    padding: 0 15px;
  }

  @include respond-to('small') {
    padding: 0 10px;
  }
}